<script setup lang="ts">
import { ref, onMounted, computed } from 'vue'
import type { PropType } from 'vue'
import { conjugate } from '@/scripts/conjugate'
import type { Kana as iKana, Kanji as iKanji } from '@/interfaces/jmdict'
import type { iConjugationObject as iConjugations } from '@/interfaces/conjugations'
import FormTable from '@/components/Dictionary/Conjugations/FormTable.vue'

const props = defineProps({
  kanji: {
    // type: Object as PropType<iKanji>,
    type: String,
    default: null
  },
  kana: {
    // type: Object as PropType<iKana[]>,
    type: String,
    default: null
  },
  conjType: {
    type: String,
    default: 'verb'
  }
})

onMounted(() => {})

//@ts-ignore
const conjugations: iConjugations = computed(() => {
  const obj = { verb: props.kanji, reading: props.kana }
  switch (props.conjType) {
    case 'verb':
      return conjugate.verb(obj)
    case 'iAdjective':
      return conjugate.i_adjective(obj)
    case 'naAdjective':
      return conjugate.na_adjective(obj)
    default:
      return null
  }
})
</script>

<template>
  <div class="container">
    <div class="row">
      <div class="col">Conjugations</div>
    </div>
    <div class="row">
      <div class="col">
        <!-- {{ conjugations }} -->
        <FormTable type="Indicative" :form="conjugations.indicative"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Presumptive" :form="conjugations.presumptive"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Progressive" :form="conjugations.progressive"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Imperative" :form="conjugations.imperative"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Request" :form="conjugations.request"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Potential" :form="conjugations.potential"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Volitional" :form="conjugations.volitional"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Causitive" :form="conjugations.causative"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Passive" :form="conjugations.passive"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Provisional" :form="conjugations.provisional"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Conditional" :form="conjugations.conditional"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Adverb" :form="conjugations.adverb"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="atrributive" :form="conjugations.attributive"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Become" :form="conjugations.become"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Looks" :form="conjugations.looks"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Noun" :form="conjugations.noun"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Too" :form="conjugations.too"></FormTable>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <FormTable type="Unbearably" :form="conjugations.unbearably"></FormTable>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss"></style>
