<script setup lang="ts">
import { ref, onMounted, watch, inject } from 'vue'
import type { Word } from '@/interfaces/jmdict'
import DictionaryEntry from '@/components/Dictionary/DictionaryEntry.vue'
import { useJmdictStore } from '@/stores/jmdict'
const jmdictStore = useJmdictStore()
import { useRoute } from 'vue-router'
import Notify from '@/plugins/Notify'
const route = useRoute()
const words = ref<Word[]>([])
const notify = inject('notify')

onMounted(() => {
  doSearch()
})

watch(
  () => route.params.word,
  (newVal, oldVal) => {
    doSearch()
  },
  { deep: true }
)

const doSearch = () => {
  words.value = jmdictStore.findWord(route.params.word.toString())
  if (words.value.length == 0) {
    //@ts-ignore
    notify.error('Nothing found for ' + route.params.word.toString())
  }
}
</script>

<template>
  <div class="container">
    <div class="row" v-for="(word, index) in words" :key="index">
      <div class="col">
        <DictionaryEntry :word="word"></DictionaryEntry>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss"></style>
