<script setup lang="ts">
import { ref, onMounted } from 'vue'
import { useJmdictStore } from '@/stores/jmdict'
import type { PropType } from 'vue'
const jmdictStore = useJmdictStore()
import Tag from 'primevue/tag'
import type { Word, Kanji as iKanji, Kana as iKana } from '../../interfaces/jmdict'

const props = defineProps({
  kanji: {
    type: Object as PropType<iKanji>,
    default: null
  },
  kana: {
    type: Object as PropType<iKana[]>,
    default: null
  }
})

onMounted(() => {})
</script>

<template>
  <br /><span class="kanji">{{
    //@ts-ignore
    kanji[jmdictStore.uglyJmdict['text']]
  }}</span
  ><span class="kana"
    >[{{
      //@ts-ignore
      kana.map((k: iKana) => k[jmdictStore.uglyJmdict['text']]).join('; ')
    }}]</span
  >
  <!-- <span class="kana">{{ kana }}</span
  > -->
  <br />
  <!-- @vue-ignore -->
  <Tag
    :class="['tag', kanji[jmdictStore.uglyJmdict['common']] ? 'common' : 'uncommon']"
    :value="kanji[jmdictStore.uglyJmdict['common']] ? 'common' : 'uncommon'"
  ></Tag
  ><br /><!-- @vue-ignore -->
  <template v-for="t in kanji[jmdictStore.uglyJmdict['tags']]" :key="t">
    <Tag :value="jmdictStore.findTag(t)"></Tag>
  </template>
</template>

<style scoped lang="scss"></style>
