<script setup lang="ts">
import { ref, onMounted, computed } from 'vue'
import type { PropType } from 'vue'
import type { Word, Kanji as iKanji, Kana as iKana, Sense as iSense } from '../../interfaces/jmdict'
import WordView from '@/views/WordView.vue'
import { useJmdictStore } from '@/stores/jmdict'
const jmdictStore = useJmdictStore()
import Tag from 'primevue/tag'
import { tail } from 'lodash'
import Kanji from '@/components/Dictionary/Kanji.vue'
import Conjugations from '@/components/Dictionary/Conjugations.vue'
import { flatten, uniq } from 'lodash'

const props = defineProps({
  word: {
    type: Object as PropType<Word>,
    default: null
  }
})
onMounted(() => {})
//@ts-ignore
const mainKanji = computed(() => props.word[jmdictStore.uglyJmdict['kanji']][0])
//@ts-ignore
const otherKanji = computed(() => tail(props.word[jmdictStore.uglyJmdict['kanji']]))

const kanaForKanji = (kanji: string) => {
  //@ts-ignore
  return props.word[jmdictStore.uglyJmdict['kana']].filter(
    //@ts-ignore
    (k: iKana) =>
      //@ts-ignore
      k[jmdictStore.uglyJmdict['appliesToKanji']].includes('*') ||
      //@ts-ignore
      k[jmdictStore.uglyJmdict['appliesToKanji']].includes(kanji)
  )
}

const allTags = computed(() =>
  //@ts-ignore
  {
    return jmdictStore.findTags(
      uniq(
        flatten(
          //@ts-ignore
          props.word[jmdictStore.uglyJmdict['sense']].map(
            //@ts-ignore
            (s: iSense) => s[jmdictStore.uglyJmdict['partOfSpeech']]
          )
        )
      )
    )
  }
)

const isVerb = computed(
  () =>
    allTags.value
      .map((p: string) => p.indexOf('Godan verb') > -1 || p.indexOf('Ichidan verb') > -1)
      .filter((d: boolean) => d).length > 0
)
const isIAdjective = computed(
  () =>
    allTags.value.map((p: string) => p.indexOf('i-adjective') > -1).filter((d: boolean) => d)
      .length > 0
)
const isNaAdjective = computed(
  () =>
    allTags.value.map((p: string) => p.indexOf('na-adjective') > -1).filter((d: boolean) => d)
      .length > 0
)
</script>

<template>
  <div class="container">
    <!-- <div class="row">
      <div class="col">
        {{ word }}
      </div>
    </div> -->
    <div class="row">
      <div class="col kanjis">
        <div class="main">
          <Kanji
            :kanji="mainKanji"
            :kana="kanaForKanji(mainKanji[jmdictStore.uglyJmdict['text']])"
          ></Kanji>
        </div>
        <template v-if="otherKanji.length > 0">
          <div class="additional">
            <!-- @vue-ignore -->
            <template v-for="k in otherKanji" :key="k.id">
              <!-- @vue-ignore -->
              <Kanji :kanji="k" :kana="kanaForKanji(k[jmdictStore.uglyJmdict['text']])"></Kanji>
            </template>
          </div>
        </template>
      </div>
      <div class="col definitions">
        <!-- @vue-ignore -->
        <div
          class="row"
          v-for="(sense, index) in word[jmdictStore.uglyJmdict['sense']]"
          :key="index"
        >
          <div class="col">
            <span
              >{{ index + 1 }}:
              {{
                sense[jmdictStore.uglyJmdict['gloss']]
                  .map((g: any) => g[jmdictStore.uglyJmdict['text']])
                  .join('; ')
              }}</span
            ><br />
            <span class="pos">{{
              sense[jmdictStore.uglyJmdict['partOfSpeech']]
                .map((t: any) => jmdictStore.findTag(t))
                .join('; ')
            }}</span>
          </div>
        </div>
      </div>
      <div class="col conjugations" v-if="isVerb || isIAdjective || isNaAdjective">
        <!-- @vue-ignore -->
        <Conjugations
          :kanji="mainKanji[jmdictStore.uglyJmdict['text']]"
          :kana="kanaForKanji([jmdictStore.uglyJmdict['text']])[0][jmdictStore.uglyJmdict['text']]"
        ></Conjugations>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
@import '../../assets/scss/variables';
.kanjis {
  :deep(.tag) {
    &.common {
      background-color: green;
    }
    &.uncommon {
      background-color: orange;
    }
  }
  .main {
    :deep(.kanji) {
      font-size: 36px;
    }
  }
  .additional {
    margin-left: 15px;
    :deep(.kanji) {
      font-size: 32px;
      color: $gray-600;
    }
  }
}
.definitions {
  .pos {
    color: $gray-600;
    margin-left: 15px;
  }
}
</style>
